.br-drawer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-height: -webkit-fill-available;
  background-color: white;
  z-index: 11;
  transform: translateX(-110%);
  transition: 200ms cubic-bezier(0.6, 0.04, 0.98, 0.335);

  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: minmax(0, 1fr);

  &.active {
    transform: translateX(0);
  }

  @media (min-width: 992px) {
    height: unset;
    position: static;
    display: block;
    border-radius: 5px;
    padding: 20px;
    width: 270px;
    min-height: 0;
    transform: translateX(0);
  }
}

.br-drawer__header {
  // height: 78px;
  padding: 30px 25px 0 25px;

  @media (min-width: 992px) {
    display: none;
    padding: 0;
  }
}

.br-drawer__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 5px;
  border-bottom: 5px solid #83c3e2;
}

.br-drawer__content {
  background-color: white;
  padding: 20px 25px 30px 25px;
  margin-right: 25px;
  overflow-y: auto;

  @media (min-width: 992px) {
    padding: 0;
    margin: 0;
  }

  &::-webkit-scrollbar {
    width: 14px;
    height: 8px;
  }

  &::-webkit-scrollbar-track {
    background: #f5f5f5;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #dadada;
    border: 4px solid #f5f5f5;
    border-radius: 20px;
  }

  @media (min-width: 992px) {
    // height: 100%;
    padding: 0 0 60px 0;
  }
}

.br-drawer__title {
  font-size: rem(28px);
  font-weight: bold;
}

.br-drawer__btn-toggle {
  background-color: transparent;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 0;
  margin-left: 20px;
  cursor: pointer;

  &:focus {
    outline: 0;
  }
}

.br-drawer__footer {
  padding: 25px;
  @media (min-width: 992px) {
    padding: 0;
  }
}

// Widgets

.br-drawer-widget + .br-drawer-widget {
  margin-top: 40px;
}

.br-drawer-widget__title {
  font-size: rem(24px);
  font-weight: 700;
  margin-bottom: 18px;
  width: 100%;
  padding: 0;
  text-align: left;
  position: relative;

  @media (min-width: 992px) {
    font-size: rem(24px);
  }
}

.br-drawer-widget__content {
  transition: 200ms;

  .br-checkbox {
    display: block;
    font-size: rem(14px);
  }

  .br-checkbox + .br-checkbox {
    margin-top: 10px;
  }
}

// Search filter
.br-drawer-widget__filter-form {
  width: 100%;
  position: relative;
}

.br-drawer-widget__filter-input {
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #bfbfbf;
  width: 100%;
  line-height: 2.3;
  font-size: rem(14px);
}

